import React, { useRef, useEffect } from 'react';
import { useThree, useLoader } from '@react-three/fiber';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

const Icon3D = () => {
  const { camera, gl } = useThree();
  const iconRef = useRef();

  // Charger le modle GLTF
  const gltf = useLoader(GLTFLoader, '/baker_and_the_bridge.glb');

  // Crer OrbitControls et attacher  la camra
  useEffect(() => {
    const controls = new OrbitControls(camera, gl.domElement);
    controls.enableDamping = true; // Ajouter un effet de suivi doux
    controls.target = iconRef.current.position; // Dfinir la cible de la camra sur l'icne
    controls.update();



    // Ajuster la position de la camra
    camera.position.z = 20; // Ajuster la distance entre la camra et le modle
    camera.position.y = 5; // Ajuster la hauteur de la camra

    // Nettoyer lors du dmontage du composant
    return () => {
      controls.dispose();
    };
  }, [camera, gl.domElement]);

  return (
    <mesh ref={iconRef}>
      <primitive object={gltf.scene} />
    </mesh>
  );
};

export default Icon3D;
